import Vue from 'vue'
import Router from 'vue-router'
import QuestionPage from '@/components/QuestionPage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/:question?',
      name: 'QuestionPage',
      component: QuestionPage
    }
  ]
})



